import React from 'react';

export default ({
  children, imagepath, height,
}) => (
  <div className="banner">
    {children}
    <style jsx>{`
      .banner {
        background: linear-gradient(
          to top,
          rgba(0, 0, 0, .35) 0%,
          rgba(0, 0, 0, .25) 100%
        ),
        url(${imagepath});
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        ${(height) ? `height: ${height};` : ''}
      }
    `}
    </style>
  </div>
);



